.btn {
  padding: 0.625rem 1.25rem;
  background-color: #e45028;
  border: none;
  border-radius: 5px;
  color: #fff;
  font-size: 0.8125rem;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
}

.btn:hover,
.btn:focus {
  background-color: #c1401e;
  cursor: pointer;
  outline: none;
}

.btn_header {
  margin-right: 30px;
}

.btn_black {
  background-color: #2f3234;
}

.btn_black:hover,
.btn_black:focus {
  background-color: #1f2122;
}

.btn_reset {
  background: none;
  text-transform: initial;
  font-family: 'Ubuntu-Light';
}

.btn_reset:hover,
.btn_reset:focus,
.btn_reset:active {
  background: none;
  border: none;
  outline: none;
}

.radio-group {
  display: flex;
}

.radio {
  margin-right: 1.5625rem;
}

.radio:last-child {
  margin-right: 0;
}

.radio__content {
  display: flex;
}

.radio__input {
  position: absolute;
  opacity: 0;
}

.radio__input:checked + .radio__content .radio__btn:after {
  opacity: 1;
}

.radio__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 0.9375rem;
  height: 0.9375rem;
  border-radius: 50%;
  background-color: #fff;
  margin-right: 0.625rem;
}

.radio__btn:after {
  content: '';
  display: block;
  width: 0.4375rem;
  height: 0.4375rem;
  border-radius: 50%;
  background: #e45028;
  opacity: 0;
  transition: 0.3s;
}

.checkbox {
  display: flex;
}

.checkbox__input {
  position: absolute;
  opacity: 0;
  margin-right: 10px;
}

.checkbox__input:checked + .checkbox__content .checkbox__btn .checkbox__svg {
  color: #e45028;
  opacity: 1;
}

.checkbox__content {
  display: flex;
}

.checkbox__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 15px;
  height: 15px;
  background-color: #fff;
  margin-right: 10px;
}

.checkbox__btn .checkbox__svg {
  color: #e45028;
  width: 10px;
  height: 10px;
  opacity: 0;
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: '';
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.pages-switcher {
