.mobile-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mobile-nav__item {
  text-align: center;
}

.mobile-nav__item:after {
  content: '';
  display: block;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #fff;
  margin: 30px 0;
  margin-left: calc(50% - 3px);
}

.mobile-nav__item:last-child:after {
  display: none;
}

.mobile-nav__link {
  color: #fff;
  font-size: 1.5rem;
  text-align: center;
  text-transform: uppercase;
}

* {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  width: 100%;
  height: 100%;
  font-family: Ubuntu-Regular;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.section {
  height: 100vh;
  min-height: 40.625rem;
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.main-wrapper {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.hidden {
  overflow: hidden;
}

@media screen and (max-width: 768px) {
  .pages-switcher {
    display: none;
  }

  .nav {
    display: none;
  }

  .mobile-menu-btn {
    display: block;
  }

  .intro-slide {
    flex-direction: column;
    padding: 0 10%;
  }

  .intro-slide__left {
    margin-right: 0;
  }

  .intro-slide__right {
    text-align: center;
  }

  .heading {
    margin-top: 0;
    line-height: 1;
    margin-bottom: 30px;
    text-align: center;
  }

  .heading__subheading {
    font-size: 3.375rem;
    text-align: center;
  }

  .feedback__item {
    width: 50%;
    height: 25%;
  }

  .feedback__back {
    text-align: center;
    padding: 1.875rem 0.625rem;
  }

  .feedback__heading {
    margin-bottom: 0.9375rem;
  }

  .slider__item {
    flex-direction: column;
    text-align: center;
  }

  .slider__btn_left {
    left: 1.25rem;
  }

  .slider__btn_right {
    right: 1.25rem;
  }

  .slider__img {
    width: 70%;
  }

  .slider__description {
    width: 75%;
  }

  .slider__info {
    justify-content: center;
  }

  .ingredients {
    left: 4.375rem;
    top: 0.625rem;
  }

  .slider__kkal {
    top: 0.625rem;
    left: 75%;
  }

  .team {
    width: 100%;
    text-align: center;
  }

  .team__person-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .team__person-photo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 0;
    margin-bottom: 1.25rem;
  }

  .menu__item.active .menu__elem-desc {
    width: 18.75rem;
  }

  .form {
    flex-direction: column;
    padding: 0 15%;
  }

  .form__column {
    margin-right: 0;
  }
}

@media screen and (max-width: 480px) {
  .intro-slide__left {
    margin-bottom: 20px;
  }

  .heading {
    font-size: 3.625rem;
  }

  .heading_h2 {
    font-size: 2.25rem;
  }

  .advantages {
    flex-direction: column;
  }

  .advantages__item {
    flex-direction: row;
    width: 100%;
    margin-right: 0;
    margin-bottom: 1.875rem;
  }

  .advantages__item:last-child {
    margin-bottom: 0;
  }

  .advantages__img {
    width: 25%;
    margin-right: 5%;
  }

  .advantages__desc {
    text-align: left;
    width: 75%;
  }

  .advantages__heading {
    margin-bottom: 0.625rem;
  }

  .feedback__heading {
    font-size: 1.5rem;
  }

  .feedback__text {
    display: none;
  }

  .slider {
    width: 95%;
  }

  .slider__btn_left {
    left: -0.875rem;
  }

  .slider__btn_right {
    right: -0.875rem;
  }

  .slider__img {
    width: 80%;
    margin-bottom: 0;
  }

  .slider__description {
    width: 95%;
  }

  .slider__text {
    font-size: 0.75rem;
  }

  .price__value {
    font-size: 3.125rem;
  }

  .ingredients {
    left: 0;
  }

  .slider__kkal {
    left: calc(100% - 70px);
  }

  .menu__item.active .menu__elem-desc {
    width: calc(100vw - 210px);
    overflow: scroll;
  }

  .menu__elem-btn {
    width: 4.375rem;
  }

  .form {
    padding: 0;
  }

  .form__row {
    justify-content: center;
  }

  .form__block_hidden-on-mobiles {
    display: none;
  }

  .container_footer {
    flex-direction: column;
  }

  .contacts {
    text-align: center;
    margin-bottom: 1.25rem;
  }
}